/* eslint-disable */
import React from "react";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';

// @material-ui/icons
import Dvr from "@material-ui/icons/Dvr";
import Delete from "@material-ui/icons/Delete";

// core components
import Button from "components/CustomButtons/Button.js";

// exp components
import NBrootsPagination from "components/@NBrootsPagination/NBrootsPagination.js";
import NBrootsDialogProcess from "components/@NBrootsDialogProcess/NBrootsDialogProcess.js";
import NBrootsMsgBox from "components/@NBrootsMsgBox/NBrootsMsgBox.js";
import NBrootsConfirm from "components/@NBrootsConfirm/NBrootsConfirm.js";


import CorpListAddPopup from "views/CommonPopup/CorpListAddPopup.js";
import CorpListDetailPopup from "views/CommonPopup/CorpListDetailPopup.js";

import Service from "utils/service/service.js"

import { BASE_URL } from 'variables/config.js';
import styles from "assets/jss/material-dashboard-pro-react/views/MngCrop/UserList/userListStyle";

export default function CropList() {

  const useStyles = makeStyles(styles);

  const classes = useStyles();

  //
  const [isLoading, setIsLoading] = React.useState(false);
  //Msg             msgInfo     : [FLG, Msg, Type:"info","success","warning","danger","primary","rose"]    
  const [msgInfo, setMsgInfo] = React.useState([false, '', 'info']);
  //Confirm         confirmInfo : [FLG, Msg, returnVal ]    
  const [confirmInfo, setConfirmInfo] = React.useState([false, '', { "opt": '', "id": '', "result": 0 }]);
  //    pageInfos   : [,]
  const [pageInfos, setPageInfos] = React.useState([0, 10, 1]);
  //
  const [lstContent, setLstContent] = React.useState([]);

  var tCorpListAddInfo = [
    false,          //
    {
      "corp": "",
      "email": "",
      "username": "",
      "furikana": "",
      "password": "",
      "isadmin": 0
    }
  ]
  const [corpListAddInfo, setCorpListAddInfo] = React.useState(tCorpListAddInfo);

  var tCorpListDetailInfo = [
    false,          //
    {
      "id": "",
    }
  ]
  const [corpListDetailInfo, setCorpListDetailInfo] = React.useState(tCorpListDetailInfo);

  //
  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {

    //
    setIsLoading(true);
    //
    formatLstData();

  }, []);    //[] 

  //APIJSONJSONFLGERRMSG
  // const formatLstData = async () => {
  const formatLstData = () => {

    var url = BASE_URL + "/api/corp_list.json";

    var data = {

    };

    Service(url, data, "GET").then(resp => {
      console.log(resp);
      // 
      if (resp.status === 0) {

        var info = resp.infos;

        //    pageInfos   : [,]
        setPageInfos([info.count, info.pageSize, info.pageNo]);

        //Data
        var lst = info.dataRows;
        lst.map(item => {
          return {
            ...item,
            //
            isEdit: false,
            chkResultName: false,
          }
        });

        setLstContent(lst);
      } else {
        //API      
      }

      setIsLoading(false);
    }).catch(resp => {
      //
      // console.log(resp);

      setIsLoading(false);

      setMsgInfo([true, resp, 'danger']);

    });

  };


  //
  const handleClickStartAdd = () => {
    corpListAddInfo[0] = true;
    corpListAddInfo[2] = false;
    setCorpListAddInfo([...corpListAddInfo]);
  }


  //Popup
  React.useEffect(() => {
    //
    function doAdd(obj) {
      setIsLoading(true);
      console.log("Update : " + obj);
      setTimeout(() => {

        setMsgInfo([true, '', 'success']);
        setIsLoading(false);

      }, 1000);
    }

    if (corpListAddInfo[2] === true)
      doAdd(corpListAddInfo);

  }, [corpListAddInfo]);

  //Popup
  const handleClickStartEdit = () => {
    corpListDetailInfo[0] = true;
    corpListDetailInfo[2] = false;
    setCorpListDetailInfo([...corpListDetailInfo]);

  }

  //Popup
  React.useEffect(() => {
    //
    function doUpdate(obj) {
      setIsLoading(true);
      console.log("Update : " + obj);
      setTimeout(() => {

        setMsgInfo([true, '', 'success']);
        setIsLoading(false);

      }, 1000);
    }

    if (corpListDetailInfo[2] === true)
      doUpdate(corpListDetailInfo);

  }, [corpListDetailInfo]);

  //
  // pageInfos   : [,]
  const handleChangePageStart = (page) => {

    if (page.disabled) return;
    if (page.text === pageInfos[2]) return;

    setIsLoading(true);

    setTimeout(() => {

      if (page.text === 'Prev') {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] - 1]);
      } else if (page.text === 'Next') {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] + 1]);
      } else {
        setPageInfos([pageInfos[0], pageInfos[1], page.text]);
      }

      setIsLoading(false);

    }, 500);
  }



  // DEL
  const handleClickStartDel = (obj) => {

    var index = lstContent.indexOf(obj);

    setConfirmInfo([true, "",
      {
        "opt": "del",
        "id": index,
        "result": 0
      }
    ]);
  };





  //
  React.useEffect(() => {
    //
    function doDel() {
      setIsLoading(true);
      console.log("Del : " + confirmInfo[2].id);
      setTimeout(() => {

        formatLstData();
        setMsgInfo([true, '', 'success']);

      }, 1000);
    }

    //
    if (confirmInfo[2].opt === "del" && confirmInfo[2].result === 1) {
      doDel();
    }

  }, [confirmInfo]);


  return (
    <Box>
      <Grid container spacing={1}>
        {/*  */}
        <Grid item xs={6} sm={6} className={classes.searchBox}>
        </Grid>
        {/*  */}
        <Grid item xs={6} sm={6} className={classes.optBox}>
          <Button color="warning" onClick={() => handleClickStartAdd()}></Button>
        </Grid>
        {/*  */}
        <Grid item xs={12} className={classes.pageJumpBox}>
          {/* [,] */}
          <NBrootsPagination
            count={pageInfos[0]}
            pagesize={pageInfos[1]}
            current={pageInfos[2]}
            onClick={handleChangePageStart}
            color="warning"
          />
        </Grid>
      </Grid>

      <Box xs={12} className={classes.tableBox}>
        <Paper className={classes.tableCardBody}>
          <TableContainer>

            <Table stickyHeader className={classes.table}>
              <TableHead>
                <TableRow>
                  <TableCell className={classes.tableHead} style={{ width: "15%" }}></TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "10%" }}></TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "45%" }}></TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "20%" }}></TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "10%" }}></TableCell>
                </TableRow>
              </TableHead>
              <TableBody className={classes.tableBody}>
                {lstContent ? lstContent.map((item, key) =>
                  <TableRow className={classes.tableRow} key={key}>
                    <TableCell className={classes.tableCellLeft} >{item.corpname}</TableCell>
                    <TableCell className={classes.tableCell} >{item.postcode}</TableCell>
                    <TableCell className={classes.tableCellLeft} >{item.address}</TableCell>
                    <TableCell className={classes.tableCell} >{item.tel}</TableCell>
                    <TableCell className={classes.tableCell} >
                      <Tooltip id="tooltip-top" title="" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartEdit(item) }} color="warning" >
                          <Dvr />
                        </Button>
                      </Tooltip>
                      <Tooltip id="tooltip-top" title="" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartDel(item) }} color="danger">
                          <Delete />
                        </Button>
                      </Tooltip>
                    </TableCell>
                  </TableRow>
                ) : null}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>


      </Box>
      {/* Popup */}
      <CorpListDetailPopup info={corpListDetailInfo} setInfo={setCorpListDetailInfo} />
      {/* Popup */}
      <CorpListAddPopup info={corpListAddInfo} setInfo={setCorpListAddInfo} />
      {/*  */}
      <NBrootsConfirm info={confirmInfo} setInfo={setConfirmInfo} />
      <NBrootsDialogProcess open={isLoading} color="warning" />
      <NBrootsMsgBox open={msgInfo[0]} message={msgInfo[1]} color={msgInfo[2]} close
        closeNotification={() => setMsgInfo([false, msgInfo[1], msgInfo[2]])} />
    </Box>
  );
}
